.news-item {
  vertical-align: middle;
  border-top: solid 1px #e0e0e0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.9em;
}
.news-item .title {
  text-decoration: none;
  color: #202020;
  -ms-flex-positive: 4;
      flex-grow: 4;
  -ms-flex-preferred-size: 400px;
      flex-basis: 400px;
}

.news-item .title:hover{
  color: #db4c3f;
} 

.news-item .user {
  font-size: 0.9em;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-align: end;
      align-items: flex-end;
  text-align: right;
}

.news-item .post-time {
  font-size: 0.9em;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-align: end;
      align-items: flex-end;
  text-align: right;
}

.news-item--empty .title {
  background-color: #e2e2e2;
}

.news-item--empty .post-time, .news-item--empty .user {
  background-color: #e2e2e2;
  margin-left: 20px;
  min-width: 100px;
}


